.notify {
    position: absolute;
    width: 300px;
    height: 400px;
    border: 1px solid black;
    border-radius: 5px;
    padding: 5px;
    top: 80px;
    background: white;
    right: 20px;
}
.notify h3 {
    font-size: 26px;
}
.notifyBody {
    display: flex;
    flex-direction: column;
}
.notifyParent {
    width: 40px;
    height: 40px;
    background: rgb(216, 216, 216);
    margin-right: 20px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
}
.notifyicon {
    font-size: 24px;
    cursor: pointer;
    color: black;
    border-radius: 50%;
}
.notifyParent:hover {
    background: rgb(121, 121, 121);
}
.notify div {
    margin-top: 5px;
}